import React from 'react'
import GraduateIcon from 'react-icons/lib/fa/graduation-cap'
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap'
import { injectState, provideState } from 'reaclette'
import { Link } from 'react-router-dom'

const withState = provideState({
  initialState: () => ({
    dropdownOpen: false,
  }),
  effects: {
    toggle: () => state => ({
      ...state,
      dropdownOpen: !state.dropdownOpen,
    }),
  },
})

const AccountMenu = ({ effects, state, history }) => (
  <Dropdown
    nav
    direction="down"
    isOpen={state.dropdownOpen}
    toggle={effects.toggle}
  >
    <DropdownToggle
      nav
      caret
      style={{
        color: '#e2e3e4',
      }}
      className="hvr-icon-fade"
    >
      <GraduateIcon size="20" className="hvr-icon" /> &nbsp; Rviser un module
    </DropdownToggle>
    <DropdownMenu style={{ backgroundColor: '#222534' }}>
      <DropdownItem
        style={{ color: '#e2e3e4' }}
        className="hvr-bounce-to-right"
      >
        <Link to={`/examen`} style={{ color: 'white', textDecoration: 'none' }}>
          Chercher un QCM
        </Link>
      </DropdownItem>
      <DropdownItem
        style={{ color: '#e2e3e4' }}
        className="hvr-bounce-to-right"
      >
        <Link
          to={`/marevision`}
          style={{ color: 'white', textDecoration: 'none' }}
        >
          Ma rvision
        </Link>
      </DropdownItem>
    </DropdownMenu>
  </Dropdown>
)

export default withState(injectState(AccountMenu))
